body{
    margin: 0;
    padding: 0;
}
.register-page{
    margin-left: 350px;
}
.login-form{
    height: 70%;
    padding: 35px;
    border: 1px solid gray;
    border-radius: 5px;
}
.login-child{
    margin: 20px 5px;
}

.click-button{
    display: flex;
    align-items:center;
    justify-content: center;
}
.click-button a{
    font-weight: 500;
    color: rgb(2, 188, 212);
    margin: 0 15px;
    text-decoration: none;
}
.click-button a:hover{
    font-weight: 600;
    color: rgb(7, 123, 128);
}
.click-button button{
    margin: 0 15px;
    background-color: rgb(0, 225, 255);
    border: none;
    color: black;
    font-weight: 600;
}
.click-button button:hover{
    background-color: rgb(7, 190, 214);
    color: black;
}